.icon-angle{
    border-left: 3px solid $main;
    border-top: 3px solid transparent;
    border-bottom: 3px solid transparent;
}

.icon-dots{
    &:before, &:after{
        content: "";
        display: block;
        width: 6px;
        height: 6px;
        background: $main;
        border-radius: 50%;
    }
    span{
        display: block;
        width: 6px;
        height: 6px;
        background: $main;
        border-radius: 50%;
        margin: 3px 0;        
    }
}

.icon-dot{
    width: 25px;
    height: 25px;
    background: $warning;
    border-radius: 50%;    
}

.icon-round-border{
    width: 35px;
    height: 35px;
    border: 1px solid $warning;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}

.rotate-90{
    transform: rotate(90deg);
}


